/* http://meyerweb.com/eric/tools/css/reset/
   v2.0-modified | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* make sure to set some focus styles for accessibility */
:focus {
    outline: 0;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}

body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
    -webkit-appearance: none;
    -moz-appearance: none;
}

input[type=search] {
    -webkit-appearance: none;
    -moz-appearance: none;
    box-sizing: content-box;
}

textarea {
    overflow: auto;
    vertical-align: top;
    resize: vertical;
}

/**
 * Correct `inline-block` display not defined in IE 6/7/8/9 and Firefox 3.
 */

audio,
canvas,
video {
    display: inline-block;
    *display: inline;
    *zoom: 1;
    max-width: 100%;
}

/**
 * Prevent modern browsers from displaying `audio` without controls.
 * Remove excess height in iOS 5 devices.
 */

audio:not([controls]) {
    display: none;
    height: 0;
}

/**
 * Address styling not present in IE 7/8/9, Firefox 3, and Safari 4.
 * Known issue: no IE 6 support.
 */

[hidden] {
    display: none;
}

/**
 * 1. Correct text resizing oddly in IE 6/7 when body `font-size` is set using
 *    `em` units.
 * 2. Prevent iOS text size adjust after orientation change, without disabling
 *    user zoom.
 */

html {
    font-size: 100%; /* 1 */
    -webkit-text-size-adjust: 100%; /* 2 */
    -ms-text-size-adjust: 100%; /* 2 */
}

/**
 * Address `outline` inconsistency between Chrome and other browsers.
 */

a:focus {
    outline: thin dotted;
}

/**
 * Improve readability when focused and also mouse hovered in all browsers.
 */

a:active,
a:hover {
    outline: 0;
}

/**
 * 1. Remove border when inside `a` element in IE 6/7/8/9 and Firefox 3.
 * 2. Improve image quality when scaled in IE 7.
 */

img {
    border: 0; /* 1 */
    -ms-interpolation-mode: bicubic; /* 2 */
}

/**
 * Address margin not present in IE 6/7/8/9, Safari 5, and Opera 11.
 */

figure {
    margin: 0;
}

/**
 * Correct margin displayed oddly in IE 6/7.
 */

form {
    margin: 0;
}

/**
 * Define consistent border, margin, and padding.
 */

fieldset {
    border: 1px solid #c0c0c0;
    margin: 0 2px;
    padding: 0.35em 0.625em 0.75em;
}

/**
 * 1. Correct color not being inherited in IE 6/7/8/9.
 * 2. Correct text not wrapping in Firefox 3.
 * 3. Correct alignment displayed oddly in IE 6/7.
 */

legend {
    border: 0; /* 1 */
    padding: 0;
    white-space: normal; /* 2 */
    *margin-left: -7px; /* 3 */
}

/**
 * 1. Correct font size not being inherited in all browsers.
 * 2. Address margins set differently in IE 6/7, Firefox 3+, Safari 5,
 *    and Chrome.
 * 3. Improve appearance and consistency in all browsers.
 */

button,
input,
select,
textarea {
    font-size: 100%; /* 1 */
    margin: 0; /* 2 */
    vertical-align: baseline; /* 3 */
    *vertical-align: middle; /* 3 */
}

/**
 * Address Firefox 3+ setting `line-height` on `input` using `!important` in
 * the UA stylesheet.
 */

button,
input {
    line-height: normal;
}

/**
 * Address inconsistent `text-transform` inheritance for `button` and `select`.
 * All other form control elements do not inherit `text-transform` values.
 * Correct `button` style inheritance in Chrome, Safari 5+, and IE 6+.
 * Correct `select` style inheritance in Firefox 4+ and Opera.
 */

button,
select {
    text-transform: none;
}

/**
 * 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio`
 *    and `video` controls.
 * 2. Correct inability to style clickable `input` types in iOS.
 * 3. Improve usability and consistency of cursor style between image-type
 *    `input` and others.
 * 4. Remove inner spacing in IE 7 without affecting normal text inputs.
 *    Known issue: inner spacing remains in IE 6.
 */

button,
html input[type="button"], /* 1 */
input[type="reset"],
input[type="submit"] {
    -webkit-appearance: button; /* 2 */
    cursor: pointer; /* 3 */
    *overflow: visible;  /* 4 */
}

/**
 * Re-set default cursor for disabled elements.
 */

button[disabled],
html input[disabled] {
    cursor: default;
}

/**
 * 1. Address box sizing set to content-box in IE 8/9.
 * 2. Remove excess padding in IE 8/9.
 * 3. Remove excess padding in IE 7.
 *    Known issue: excess padding remains in IE 6.
 */

input[type="checkbox"],
input[type="radio"] {
    box-sizing: border-box; /* 1 */
    padding: 0; /* 2 */
    *height: 13px; /* 3 */
    *width: 13px; /* 3 */
}

/**
 * 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.
 * 2. Address `box-sizing` set to `border-box` in Safari 5 and Chrome
 *    (include `-moz` to future-proof).
 */

input[type="search"] {
    -webkit-appearance: textfield; /* 1 */ /* 2 */
    box-sizing: content-box;
}

/**
 * Remove inner padding and search cancel button in Safari 5 and Chrome
 * on OS X.
 */

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}

/**
 * Remove inner padding and border in Firefox 3+.
 */

button::-moz-focus-inner,
input::-moz-focus-inner {
    border: 0;
    padding: 0;
}

/**
 * 1. Remove default vertical scrollbar in IE 6/7/8/9.
 * 2. Improve readability and alignment in all browsers.
 */

textarea {
    overflow: auto; /* 1 */
    vertical-align: top; /* 2 */
}

/**
 * Remove most spacing between table cells.
 */

table {
    border-collapse: collapse;
    border-spacing: 0;
}

html,
button,
input,
select,
textarea {
    color: #222;
}

::selection {
    background: #b3d4fc;
    text-shadow: none;
}

img {
    vertical-align: middle;
}

fieldset {
    border: 0;
    margin: 0;
    padding: 0;
}

textarea {
    resize: vertical;
}

.chromeframe {
    margin: 0.2em 0;
    background: #ccc;
    color: #000;
    padding: 0.2em 0;
}


.card-container {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, 520px);
  grid-gap: 10px;
  gap: 10px;
  justify-content: center;
  background-color: #f0f0f0;
}

@media only screen and (max-width: 550px) {
  .card-container {
    grid-template-columns: 1fr;
  }
}

.card {
  width: 500px;
  display: grid;
  grid-template-columns: 240px 1fr;
  grid-gap: 15px;
  gap: 15px;
  padding: 10px;
  border-radius: 10px;
  margin-top: 10px;
  font-family: 'Roboto', sans-serif;
  background-color: #fff;
}

.card__image-placeholder {
  background-color: rgb(223, 223, 223);
  width: 240px;
  height: 180px;
  margin: auto;
}

.card__img {
  border-radius: 5px;
  cursor: pointer;
}

.card__text {
  position: relative;
}

.card__title {
  font-weight: bold;
  font-size: 22px;
  margin-bottom: 10px;
  -webkit-transition: color ease-in-out .3s;
  transition: color ease-in-out .3s;
}

.card:hover .card__title {
  color: lightskyblue;
}

.card__address {
  color: rgb(105, 105, 105);
}

.card__price {
  position: absolute;
  bottom: 0;
}

.card__open-menu {
  position: absolute;
  width: 30px;
  height: 20px;
  bottom: 0;
  right: 0;
  cursor: pointer;
  -webkit-transition: fill ease-in-out .3s, right ease-in-out .3s;
  transition: fill ease-in-out .3s, right ease-in-out .3s;
}

.card:hover .card__open-menu {
  fill: lightskyblue;
  right: 5px;
}

@media only screen and (max-width: 550px) {
  .card {
    display: flex;
    flex-direction: column;
    width: 250px;
    margin: auto;
    margin-top: 10px;
  }
  .card__text {
    width: 240px;
    margin: auto;
    margin-top: 10px;
  }
  .card__price {
    position: relative;
    margin-top: 10px;
  }
}


.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0,0,0,0.7);
  font-family: 'Roboto', sans-serif;

  cursor: pointer;
}

.popup__content {
  position: relative;
  width: 70%;
  padding: 30px;
  background-color: white;
  border-radius: 15px;

  display: grid;
  grid-template-columns: 2fr 3fr;
  grid-gap: 15px;
  gap: 15px;

  line-height: 20px;

  cursor: default;
}

.popup__close-btn {
  position: absolute;
  z-index: 1;
  top: 10px;
  right: 10px;

  cursor: pointer;
}

.popup__img-container {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.popup__img {
  width: 100%;
  height: 70vh;
  object-fit: contain;
  margin: auto;
}

.img-container {
  position: relative;
}

.img-container__btn {
  position: absolute;
  width: 30px;
  height: 20px;
  bottom: 50%;

  cursor: pointer;
}

.img-container__btn_left {
  left: 0;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.img-container__btn_right {
  right: 0;
}

.img-container__circle-list {
  width: 100%;
  display: flex;
  justify-content: center;
  position: absolute;
  bottom: 0;
  padding-bottom: 10px;
}

.popup__text {
  position: relative;
  max-height: 70vh;
  overflow-y: scroll;
}

.popup__title {
  font-weight: bold;
  font-size: 22px;
  margin-bottom: 5px;
}

.popup__address {
  color: rgb(105, 105, 105);
  margin-bottom: 10px;
}

.popup__description-label {
  font-weight: 600;
}

.popup__description {
  margin-bottom: 10px;
}

.popup__seller-name {
  margin-bottom: 15px;
}

.popup__price {
  margin-bottom: 20px;
}

@media only screen and (max-width: 550px) {
  .popup__content {
    grid-template-rows: 50% 50%;
    grid-template-columns: unset;
    height: 80vh;
    padding: 15px;
  }
  .popup__img-container {
    height: 100%;
    order: 1;
  }
  .popup__img {
    height: 100%;
  }
  .popup__text {
    order: 2;
  }
}

.circle {
  width: 15px;
  height: 15px;
  background-color: #fff;
  border-radius: 50%;

  display: flex;
  justify-content: center;
  flex-direction: column;

  margin-left: 15px;
  border: 1px solid black;

  cursor: pointer;
}

.circle__inner {
  width: 60%;
  height: 60%;
  background-color: lightskyblue;
  border-radius: 50%;
  margin: auto;
}

